// ** React Imports
import {
  Button,
  Card,
  CardBody,
  Col,
  Form,
  FormFeedback,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  Row,
  Table
} from 'reactstrap'
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import axios from '@src/configs/axios/axiosConfig'
import { useEffect, Fragment, useState } from 'react'
import toast from 'react-hot-toast'
import { getData } from './store/index'
import { useForm, Controller } from "react-hook-form"
import { useDispatch } from 'react-redux'
import classnames from 'classnames'
import { Edit, Trash } from 'react-feather'
import { activeOrganizationid, orgUserId, activeOrganization } from '@src/helper/sassHelper'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
const activeOrg = activeOrganization()
const MySwal = withReactContent(Swal)
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

const taxRate = () => {
  // ** States
  const [formModal, setFormModal] = useState(false)
  const [selected, setSelected] = useState(null)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const schema = yup.object().shape({
    name: yup.string().required("Please Enter Name"),
    percentage: yup.string().required("Please Enter Tax Percentage"),
    id: yup.string(),
    createdby: yup.string().default(userId),
    updatedby: yup.string().default(userId),
    organizationId: yup.string().default(activeOrgId)
  })

  const { handleSubmit, formState: { errors }, control, setValue } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const setDefaultTax = (id) => {
    setBlock(true)
    axios.post('/organizationprefernces/createandupdate', { organizationId: activeOrgId, updatedBy: userId, defaultTaxId: id })
      .then(() => {
        setBlock(false)
        toast.success('Tax Rate Upated Successfully', { position: 'bottom-right' })
      })
      .catch(() => {
        toast.error('Event Failed!', { position: 'bottom-right' })
      })
  }
  const dispatch = useDispatch()
  const [data, setData] = useState([])
  const [defaulttaxid, setDefaultTaxId] = useState(null)

  const getTaxList = () => {
    axios.post('/taxgroups/list', { country: activeOrg.countryshortname })
      .then((res) => {
        setData(res.data.taxgroups)
      })
      .catch((err) => { console.log(err) })
  }

  const deletefn = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await axios.post('/taxgroups/delete', { id, updatedBy: userId })
        getTaxList()
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Document Status has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
        return true
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        return false
      }
    })
  }

  useEffect(async () => {
    const response = await dispatch(getData(activeOrgId))
    const res = response.payload?.data
    setDefaultTaxId(res?.defaulttaxid || '')

    await getTaxList()

  }, [])

  const resetForm = () => {
    setValue('name', '')
    setValue('percentge', '')
    setValue('id', '')
    setValue('organizationId', activeOrgId)
    setValue('createdby', userId)
    setValue('updatedby', userId)
  }

  const closeModal = () => {
    setFormModal(false)
    resetForm()
  }

  const getRow = (fieldLabel, fieldName, reqflag = true) => {
    return (
      <Col md={12}>
        <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Col>
    )
  }

  const openModal = () => {
    setFormModal(true)
    resetForm()
  }

  const openEditModal = card => {
    setSelected(card)
    setFormModal(true)
    setValue('name', card.groupname)
    setValue('id', card.id)
    setValue('percentage', card.percentage)
    setValue('organizationId', activeOrgId)
    setValue('createdby', userId)
    setValue('updatedby', userId)

  }

  const onSubmit = async data => {
    if (selected !== null) {
      axios.post('/taxgroups/update', data)
        .then(() => {
          getTaxList()
          closeModal()
          toast.success('Documents Place Updated Successfully', { position: 'bottom-right' })
        })
        .catch((err) => { console.log(err) })
      setSelected(null)
    } else {
      axios.post('/taxgroups/create', data)
        .then(() => {
          getTaxList()
          closeModal()
          toast.success('Document Place Added Successfully', { position: 'bottom-right' })
        })
        .catch((err) => { console.log(err) })
    }

  }

  return (
    <UILoader blocking={block} loader={<Loader />}>
      <Card>
        <CardBody className='py-25'>
          <Col className='d-flex justify-content-between'>
            <h5>Tax Rates</h5>
            {activeOrg.countryshortname !== 'IN' &&
              <Button size='sm' color='primary' outline onClick={() => openModal()}>Add</Button>
            }
          </Col>
        </CardBody>
        <CardBody>
          <Table>
            <thead>
              <tr>
                <th>Default</th>
                <th>Tax Rate</th>
                <th>Percentage</th>
                {activeOrg.countryshortname !== 'IN' &&
                  <th>Actions</th>
                }
              </tr>
            </thead>
            <tbody>
              {data.map((item, index) => {
                return (
                  <tr key={index}>
                    <td>
                      <Input type='radio' value={item.id} name='taxrate' defaultChecked={defaulttaxid === item.id} onChange={() => { setDefaultTax(item.id) }} />
                    </td>
                    <td>{item.name}</td>
                    <td>{item.percentage || 0} %</td>
                    {activeOrg.countryshortname !== 'IN' &&
                      <td>
                        <div className='d-flex'>
                          <Col onClick={() => { openEditModal(item) }} className='cursor-pointer'>
                            <Edit size={16} />
                          </Col>
                          <Col onClick={() => { deletefn(item.id) }} className='cursor-pointer'>
                            <Trash size={16} />
                          </Col>
                        </div>
                      </td>
                    }
                  </tr>
                )
              })
              }
            </tbody>
          </Table>
          {data.length === 0 &&
            <Row className='text-center mt-1'>
              <h5>No Records</h5>
            </Row>
          }

          <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
            <ModalHeader toggle={() => closeModal()}>Document In - Out</ModalHeader>
            <ModalBody>
              {
                Object.keys(errors).map((key) => {

                  return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

                })
              }
              <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>

                {getRow('Tax Name', 'name', true)}

                {getRow('Tax Percentage', 'percentage', true)}

                <Col className='mt-2 pt-1' xs={12}>
                  <Button type='submit' className='me-1' color='primary'>
                    Submit
                  </Button>
                  <Button className='me-1' color='warning' onClick={() => closeModal()}>
                    Cancel
                  </Button>
                </Col>

              </Row>
            </ModalBody>
          </Modal>

        </CardBody>
      </Card>
    </UILoader>
  )
}

export default taxRate
